import request from '@/request/api'
/**===========基本信息接口 */
//获取列表
export function getListApi(data = {}) {
    return request({
        url: '/maintain/SpareInstockAction/queryList.htm',
        method: 'post',
        data: data
    })
}
//详情
export function getDetailsApi(id = "") {
    return request({
        url: '/maintain/SpareInstockAction/detail.htm',
        method: 'post',
        params: { id: id }
    })
}
//删除
export function deleteInstock(id = "") {
    return request({
        url: '/maintain/SpareInstockAction/delete.htm',
        method: 'post',
        params: { id: id }
    })
}

//提交流程
export function addInstock(data = {}) {
    return request({
        url: '/maintain/SpareInstockAction/addSpareInstock.htm',
        method: 'post',
        data: data
    })
}
//保存草稿
export function updateInstock(data = {}) {
    return request({
        url: '/maintain/SpareInstockAction/updateSpareInstock.htm',
        method: 'post',
        data: data
    })
}


