<template>
  <div class="spareStorage h100">
    <!-- <publicCrumbs :crumbsData="crumbsData"></publicCrumbs> -->
    <div class="page-main-box">
      <KlTable
        v-bind="tableConfig"
        v-show="pageType == 'list'"
        ref="klTable"
      ></KlTable>
      <editPage
        v-if="pageType !== 'list'"
        @goBackPage="goBackPage"
        :rightBtnArr="rightBtnArr"
      >
        <addInfo
          ref="editForm"
          :pageType="pageType"
          :formData="formData"
        ></addInfo>
      </editPage>
    </div>
  </div>
</template>

<script>
import addInfo from "./add";
import {
  getListApi, //获取列表
  getDetailsApi, // 详情
  deleteInstock, //删除
  addInstock, //添加
  updateInstock, //修改
} from "@/api/itOperations/spareStorage";
import { queryAssetTypeList } from "@/api/common";

export default {
  name: "spareStorage",
  components: { addInfo },
  data() {
    return {
      label: "",
      maxNum: 0,
      otherInfo: "",
      showMember: false,
    //   distributionDialog: false,
    //   distributionData: {},
      currentItem: {},
      orgMemberSelect: {},
      tableConfig: {
        showFixedHeight: true,
        searchObj: {
          searchMethod: (params, that) => {
            if (this.tableConfig.tableDataObj) {
              this.getTableData();
            } else {
            }
          },
          searchArr: [
            {
              type: "el-select",
              label: "备件类型",
              model: "assetTypeId",
              option: this.$store.getters.spareTypeList,
            },
            {
              type: "el-input",
              label: "备件型号",
              model: "goodsModel",
              placeholder: "型号",
            },
            {
              type: "daterange",
              label: "采购日期",
              model: "billDate",
            },
            {
              type: "el-select",
              label: "审核状态",
              model: "status",
              placeholder: "审核状态",
              option: this.$store.getters["dirtData"]("flow_type")
            },
          ],
        },
        // 查询条件参数
        queryObj: {
          method: () => {
            this.getTableData();
          },
          queryData: {
            page: 1,
            pageSize: 15, //pageCount
          },
        },
        tableDataObj: {
          tableArr: [],
          total: null,
        },
        operateArr: [
          {
            name: "新建",
            // isHide: !this.$store.getters["isBtnAuth"]("insert"),
            icon: "el-icon-plus",
            method: (name) => {
              this.toAdd();
            },
          },
        ],
        //  table表配置参数
        tableObj: {
          // 左边选择框展示
          selectionShow: false,
          // 左边序号展示
          indexShow: true,
          // table表事件参数配置
          tableMethods: {
            selection: (val) => {
              this.selectionData = val;
            },
          },

          // table表展示列配置
          showItems: [
            {
              name: "入库单号",
              prop: "orderNo",
            },
            {
              name: "采购日期",
              prop: "billDate",
            },
            {
              name: "备件类型",
              prop: "assetTypeName",
            },
            {
              name: "备件型号",
              prop: "goodsModel",
            },
            {
              name: "采购数量",
              prop: "inQty",
            },
            {
              name: "出库数量",
              prop: "outQty",
            },
            {
              name: "采购合同号",
              prop: "contractNo",
            },
            {
              name: "备注",
              prop: "remark",
            },
            {
              name: "审批人",
              prop: "auditorName",
            },
            {
              name: "流程节点",
              prop: "stage",
            },
            {
              name: "流程状态",
              prop: "flowStatusName",
              id: "status",
            },
            {
              name: "操作",
              prop: "operate",
              width: 150,
              scopeType: "button",
              fixed: "right",
              buttonArr: [
                // 最后一行操作栏数组
                {
                  name: "详情",
                  method: (row) => {
                    this.detailsInfo(row);
                  },
                  isShow: (row, that) => {
                    return true;
                  },
                  icon: "false",
                },
                {
                  name: "审批",
                  method: (row) => {
                    //0不可以审核 1可审核
                    this.detailsInfo(row);
                  },
                  isShow: (row, that) => {
                    return row.workflowIsApprover;
                  },
                  icon: "false",
                },
                {
                  name: "编辑",
                  method: (row) => {
                    this.editInfo(row);
                  },
                  isShow: (row, that) => {
                    return row.status === 2;
                  },
                  icon: "false",
                },
                {
                  name: "评分",
                  method: (row) => {
                    this.score(row);
                  },
                  isShow: (row, that) => {
                    // return true;
                    return row.status === 1;
                  },
                  icon: "false",
                },

                {
                  name: "删除",
                  method: (row) => {
                    this.deleteInfo(row);
                  },
                  isShow: (row, that) => {
                    // return true;
                    return row.status === 2;
                  },
                  icon: "false",
                },
              ],
            },
          ],
        },
        // 分页展示总条配置
        paginationObj: {
          pageSizes: [15, 30, 50, 100],
        },
        paginationIsshow: true,
        showklTable: false,
      },
      pageType: "list",
      formData: {},
    };
  },
  computed: {
    rightBtnArr() {
      let arr = [
        {
          text: "保存",
          icon: "el-icon-check",
          hide: this.pageType == "details",
          methods: () => {
            this.doSubmit(2);
          },
        },
        {
          text: "提交",
          icon: "el-icon-finished",
          hide: this.pageType == "details",
          methods: () => {
            this.doSubmit(1);
          },
        },
      ];
      return arr;
    },
  },
  watch: {
    // 对人员选择框做清空处理
    "tableConfig.queryObj.queryData.staffunName"(val) {
      if (val == "") {
        this.tableConfig.queryObj.queryData.empId = null;
      }
    },
    pageType(val) {
      if (val === "list") {
        this.getTableData();
      }
    },
  },

  mounted() {
    this.getTableData();
  },
  methods: {
    //新建
    toAdd() {
      this.pageType = "add";
      this.formData = {};
    },
    //查询数据
    getTableData() {
      let form = Object.assign({}, this.tableConfig.queryObj.queryData);
      form.pageCount = form.pageSize;
      //日期处理
      let { billDate } = form;
      if (billDate) {
        let [beginDate, endDate] = billDate;
        form.beginDate = beginDate;
        form.endDate = endDate;
      }
      this.$delete(form, "billDate");
      this.$delete(form, "pageSize");
      this.$delete(form, "staffunName");
      getListApi(form)
        .then((res) => {
          this.$set(
            this.tableConfig.tableDataObj,
            "tableArr",
            res.data.list || []
          );
          this.$set(
            this.tableConfig.tableDataObj,
            "total",
            res.data.total || 0
          );
        })
        .catch((res) => {
          this.$set(this.tableConfig.tableDataObj, "tableArr", []);
          this.$set(this.tableConfig.tableDataObj, "total", 0);
        });
    },

    //详情
    detailsInfo(row) {
      this.pageType = "details";
      this.formData = Object.assign({}, row);
    },
    //编辑
    editInfo(row) {
      this.pageType = "edit";
      this.formData = Object.assign({}, row);
    },
    //评分
    score(row) {
      this.pageType = "score";
      this.formData = Object.assign({}, row);
    },
    //删除
    deleteInfo(row) {
      this.$confirm(`是否确认删除`, "提示", {
        // this.$confirm(`是否删除【${row.trainingName}】`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          deleteInstock(row.id).then((res) => {
            this.$message.success("操作成功");
            this.getTableData();
          });
        })
        .catch((e) => {});
    },
    //点击提交
    doSubmit(submitType) {
      this.$refs.editForm
        .getFormData()
        .then((data) => {
          // console.log(data);
          this.$set(data, "submitType", submitType);
          this.$delete(data, "assetsNumber");
          this.$delete(data, "appsysName");
          const actionApi = (queryData) => {
            //addApi
            return queryData.id ? updateInstock(queryData) : addInstock(queryData);
          };
          actionApi(data).then((res) => {
            this.$message.success("操作成功！");
            if (submitType == 2) {
              this.pageType = "edit";
              if (res.data) {
                this.formData.id = res.data;
              }
              this.$refs.editForm.getDetails(this.formData.id);
            } else {
              this.pageType = "list";
            }
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },

    //返回
    goBackPage(data) {
      this.pageType = "list";
    },
  },
};
</script>

